@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@700&family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding: 0; margin: 0; box-sizing: border-box; font-family: 'Poppins', sans-serif; font-weight: 400;}


.covidTracker{float: left; width: 100%; height: 100%; display: flex; flex-flow: row wrap; justify-content:center;text-align: center; padding: 50px 0; height: 100vh; align-items: center;
    background-color: #1E1E1E;}
.covidTracker h1, .covidTracker h2{width: 100%; color: #ffffff} 
.covidTracker ul{max-width: 1000px;
    display: flex;
    flex-flow: row wrap;
    padding: 0 20px;
    list-style: none;
    grid-column-gap: 3.5%;
    -webkit-column-gap: 3.5%;
            column-gap: 3.5%;
    grid-row-gap: 30px;
    row-gap: 30px; margin: 40px auto 0;} 
.covidTracker ul li{float: left; width: 31%; background-color: #4D70C1; padding: 20px; text-transform: uppercase;    min-height: 200px;
    display: flex;
    flex-flow: row wrap;
    align-items: center;}
.covidTracker ul li:nth-child(2){background-color: #29923D;}
.covidTracker ul li:nth-child(3){background-color: #BBA92C;}
.covidTracker ul li:nth-child(4){background-color: #B25516;}
.covidTracker ul li:nth-child(5){background-color: #2C4B90;}
.covidTracker ul li:nth-child(6){background-color: #867B43;}

.card, .card__main, .card__inner{float:left; width: 100%;}
.card__name{font-size: 30px}
.card__name span{font-size: 20px;}
.card__total{font-size: 40px; text-shadow: 6px 10px 6px rgb(0 0 0 / 52%);font-family: 'Oswald', sans-serif; font-weight: 700;}
.covidTrackerInner{float: left; width: 100%;}
@media only screen and (min-width: 280px) and (max-width: 767px) {
    .covidTracker ul{grid-column-gap: 0;-webkit-column-gap: 0;column-gap: 0;}
    .covidTracker ul li{width: 100%;}
    .covidTracker{height: 100%;}
}


@media only screen and (min-width: 481px) and (max-width: 991px) {
    .covidTracker ul{grid-column-gap: 4%;-webkit-column-gap: 4%;column-gap: 4%;}
    .covidTracker ul li{width: 48%;}
    .covidTracker{height: 100%;}
}


@media only screen and (min-width: 992px) and (max-width:1024px) {
    .covidTracker{height: 100%;}
}














